<template>
  <div class="container mx-auto flex flex-col items-center bg-gray-100 p-4">
    <div class="container">
      <div class="w-full my-4"></div>
      <section>
        <div class="flex">
          <div class="max-w-xs">
            <label for="wallet" class="block text-sm font-medium text-gray-700"
              >Тикер</label
            >
            <div class="mt-1 relative rounded-md shadow-md">
              <input
                type="text"
                name="wallet"
                id="wallet"
                class="block w-full pr-10 border-gray-300 text-gray-900 focus:outline-none focus:ring-gray-500 focus:border-gray-500 sm:text-sm rounded-md"
                placeholder="Например DOGE"
                v-model="ticker"
                v-on:keydown.enter="addMain"
                v-on:input="processTicker"
              />
            </div>
            <div class="flex bg-white shadow-md p-1 rounded-md flex-wrap">
            <span @click="addMain"   v-for="item in arr2.slice(0, 4)" :key="item.id"  class="inline-flex items-center px-2 m-1 rounded-md text-xs font-medium bg-gray-300 text-gray-800 cursor-pointer">
              {{item.Symbol}}
            </span>
            <div :style="errorDiv" class="text-sm text-red-600">Такой тикер уже добавлен</div>
          </div>



          </div>
        </div>
        <button
          type="button"
          class="my-4 inline-flex items-center py-2 px-4 border border-transparent shadow-sm text-sm leading-4 font-medium rounded-full text-white bg-gray-600 hover:bg-gray-700 transition-colors duration-300 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500"
          @click="addMain"
        >
          <!-- Heroicon name: solid/mail -->
          <svg
            class="-ml-0.5 mr-2 h-6 w-6"
            xmlns="http://www.w3.org/2000/svg"
            width="30"
            height="30"
            viewBox="0 0 24 24"
            fill="#ffffff"
          >
            <path
              d="M13 7h-2v4H7v2h4v4h2v-4h4v-2h-4V7zm-1-5C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z"
            ></path>
          </svg>
          Добавить
        </button>
      </section>
      

     
        <div>
        <hr class="w-full border-t border-gray-600 my-4">
        <button v-if="page>1" @click="page=page-1" class="my-4 mx-2 inline-flex items-center py-2 px-4 border border-transparent shadow-sm text-sm leading-4 font-medium rounded-full text-white bg-gray-600 hover:bg-gray-700 transition-colors duration-300 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500">Назад</button>
        <button v-if="hasNextPage" @click="page=page+1" class="my-4 mx-2 inline-flex items-center py-2 px-4 border border-transparent shadow-sm text-sm leading-4 font-medium rounded-full text-white bg-gray-600 hover:bg-gray-700 transition-colors duration-300 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500">Вперёд</button>
        </div>
      <div>Фильтр:<input v-model="filter" /></div>
      
      
      
      
      
      
      <hr class="w-full border-t border-gray-600 my-4"
        v-if="tickers.length > 0"
      />
      <dl class="mt-5 grid grid-cols-1 gap-5 sm:grid-cols-3">
        <div
          v-for="t in filteredTickers()"
          v-bind:key="t.name"
          @click="select(t)"
          :class="{ 'border-4': sel == t }"
          class="bg-white overflow-hidden shadow rounded-lg border-purple-800 border-solid cursor-pointer"
        >
          <div class="px-4 py-5 sm:p-6 text-center">
            <dt class="text-sm font-medium text-gray-500 truncate">
              {{ t.name }}
            </dt>
            <dd class="mt-1 text-3xl font-semibold text-gray-900">
              {{ t.price }}
            </dd>
          </div>
          <div class="w-full border-t border-gray-200"></div>
          <button
            class="flex items-center justify-center font-medium w-full bg-gray-100 px-4 py-4 sm:px-6 text-md text-gray-500 hover:text-gray-600 hover:bg-gray-200 hover:opacity-20 transition-all focus:outline-none"
            @click.stop="handleDelete(t)"
          >
            <svg
              class="h-5 w-5"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
              fill="#718096"
              aria-hidden="true"
            >
              <path
                fill-rule="evenodd"
                d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z"
                clip-rule="evenodd"
              ></path></svg
            >Удалить
          </button>
        </div>
      </dl>
      <hr
        class="w-full border-t border-gray-600 my-4"
        v-if="tickers.length > 0"
      />
      <section v-if="sel" class="relative">
        <h3 class="text-lg leading-6 font-medium text-gray-900 my-8">
          {{ sel.name }} - USD
        </h3>
        <div class="flex items-end border-gray-600 border-b border-l h-64">
          <div
            v-for="(bar, idx) in normalizeGraph()"
            :key="idx"
            :style="{ height: `${bar}%` }"
            class="bg-purple-800 border w-10"
          ></div>
        </div>
        <button
          @click="sel = null"
          type="button"
          class="absolute top-0 right-0"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xmlns:xlink="http://www.w3.org/1999/xlink"
            xmlns:svgjs="http://svgjs.com/svgjs"
            version="1.1"
            width="30"
            height="30"
            x="0"
            y="0"
            viewBox="0 0 511.76 511.76"
            style="enable-background: new 0 0 512 512"
            xml:space="preserve"
          >
            <g>
              <path
                d="M436.896,74.869c-99.84-99.819-262.208-99.819-362.048,0c-99.797,99.819-99.797,262.229,0,362.048    c49.92,49.899,115.477,74.837,181.035,74.837s131.093-24.939,181.013-74.837C536.715,337.099,536.715,174.688,436.896,74.869z     M361.461,331.317c8.341,8.341,8.341,21.824,0,30.165c-4.16,4.16-9.621,6.251-15.083,6.251c-5.461,0-10.923-2.091-15.083-6.251    l-75.413-75.435l-75.392,75.413c-4.181,4.16-9.643,6.251-15.083,6.251c-5.461,0-10.923-2.091-15.083-6.251    c-8.341-8.341-8.341-21.845,0-30.165l75.392-75.413l-75.413-75.413c-8.341-8.341-8.341-21.845,0-30.165    c8.32-8.341,21.824-8.341,30.165,0l75.413,75.413l75.413-75.413c8.341-8.341,21.824-8.341,30.165,0    c8.341,8.32,8.341,21.824,0,30.165l-75.413,75.413L361.461,331.317z"
                fill="#718096"
                data-original="#000000"
              ></path>
            </g>
          </svg>
        </button>
      </section>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      ticker: "",
      tickers: [],
      sel: null,
      graph: [],
      arr:[],
      arr2:[],
      arr3:[],
      el:null,
      errorDiv:"display:none",
      page:1,
      filter:"",
      hasNextPage:true,
    }; 
     },
         
        
  
  methods: {
    addMain(){
       if((event.target)&&(event.target.name!=="wallet")&&(event.target.type!=="button")){
         console.log(event.target.textContent)
         this.add3(event.target)
       }
       else{
       this.add2();}
    },
    
    add3(evt){
      console.log(evt)
      //console.log(this.ticker)
           let el=this.arr3.some(elem => elem.toUpperCase() == evt.textContent.toUpperCase());
              //console.log(el)
          if (el==false){
            //console.log(`Добавил эллементы==== ${this.arr3}в список `)
          this.arr3.push(evt.textContent.toUpperCase())
          this.ticker=evt.textContent.toUpperCase();
          this.add();
          }
          else{
            alert("Такой эллемент уже существует")
            this.errorDiv="display:block"
          }
    },     
    add() {
           const currentTicker = {
        name: this.ticker,
        price: "-",
      };
      //this.filter='';
      this.tickers.push(currentTicker);
     
      localStorage.setItem("cryptonomicon-list",JSON.stringify(this.tickers));
      this.subscribeToUpdates(currentTicker.name);
      //this.ticker = "";
    },
    select(ticker) {
      this.sel = ticker;
      this.graph = [];
    },
    subscribeToUpdates(tickerName){
      setInterval(async () => {
        const f = await fetch(
          "https://min-api.cryptocompare.com/data/price?fsym=" +
            tickerName +
            "&tsyms=USD&api_key=49b5f8cf8b3cd9d09260be9a9763d0b6e287debd27487dec9ccc2eb37a79a8a0"
        );
        const data = await f.json();
      
        
        
        
        this.tickers.find((t) => t.name === tickerName).price =
          data.USD;
        if (this.sel?.name === tickerName) {
          this.graph.push(data.USD);
          
        }
      }, 3000);
    },
    async getGeneralList() {
     
     const m =   await fetch(
          "https://min-api.cryptocompare.com/data/all/coinlist?summary=true"
        );
        
        const data2 = await m.json();
          for (let [,value] of Object?.entries(data2?.Data)) {
           
        this.arr.push(value)}
        },
        
        
        
        
        add2() {
                 
          console.log(this.ticker)
           let el=this.arr3.some(elem => elem.toUpperCase() == this.ticker.toUpperCase());
              console.log(el)
          if (el==false){
            console.log(`Добавил эллементы==== ${this.arr3}в список `)
          this.arr3.push(this.ticker.toUpperCase())
          this.add();
          }
          else{
            alert("Такой эллемент уже существует")
            this.errorDiv="display:block"
          }
          
          
           },
          
    
    
    
    
    
        
    processTicker(){
  this.errorDiv="display:none"
  this.arr2=[];
    for (let [,value] of Object.entries(this.arr)) {
       
        let fName = value.FullName.toUpperCase()
        let fSymbol = value.Symbol.toUpperCase()
        let coincidence=fName.includes(this.ticker.toUpperCase())
        let coincidence2=fSymbol.includes(this.ticker.toUpperCase())
        if((coincidence>0)||(coincidence2>0)){
               this.arr2.push(value)
               
         }}
        
        },

    handleDelete(tickerToRemove) {
      this.tickers = this.tickers.filter((t) => t !== tickerToRemove);
    },
    filteredTickers(){
      const start=(this.page-1)*6;
      const end=this.page*6;
      const filteredTickers= this.tickers.filter(ticker=>ticker.name.includes(this.filter))
      
      this.hasNextPage=filteredTickers.length>end;
      return filteredTickers.slice(start,end)
    },
    normalizeGraph() {
      const maxValue = Math.max(...this.graph);
      const minValue = Math.min(...this.graph);
      return this.graph.map(
        (price) => 5 + ((price - minValue) * 95) / (maxValue - minValue)
      );
    },
  },
  watch:{
    filter(){this.page=1;}
    }, 
  created:function(){
    this.getGeneralList()
    const tickersData=localStorage.getItem("cryptonomicon-list");
      if(tickersData){
        this.tickers=JSON.parse(tickersData);
         this.tickers.forEach(ticker => {
           this.subscribeToUpdates(ticker.name);
         });
         
       }
       }
};
</script>


